const vscode = require('vscode');
const path = require('path');
const fs = require('fs');
const util = require('./util');

module.exports = function(context) {
    let b = {
        /**
         * package.jsondependenciesdevDependencies
         * 
         * @param {*} document 
         * @param {*} position 
         * @param {*} token 
         */
        provideHover(document, position, token) {
            return new Promise(resolve => {
                const fileName    = document.fileName;
                const workDir     = path.dirname(fileName);
                const word        = document.getText(document.getWordRangeAtPosition(position));
                util.Document['hoverPosition'] = {
                    date: new Date(),
                    fileName: fileName,
                    word: word
                };
                let url = '/api/'
                // 
                let ControllerName = util.Document.isController(document);
                if (ControllerName){
                    url += ControllerName + '/';
                } else return;
                let _text = document.lineAt(position.line).text
                let _match = _text.match(/^\s+public.+\s+?(.+?)\((.+?)?\)/)
                if (!_match) {
                    return;
                }
                else{
                    if (word != _match[1]) return;
                    url += _match[1]
                }
                setTimeout(() => {
                    let hosts = {
                        '': 'http://localhost:9076',
                        '-hz': 'http://192.168.30.9:9076',
                        '-kh': 'http://192.168.40.2:9076',
                        '-hz': 'http://hannao.gicp.net:9076',
                        '-kh': 'http://36.26.51.30:9076',
                    };
                    let heads = [];
                    let rows = {
                        '[]({0}{1})': [],
                        '[]({0}/weblog/WebLogIndxe?uri={1})': [],
                    };
                    for (const key in hosts) {
                        heads.push(key)
                        const _url = hosts[key];
                        for (const fmt in rows) {
                            let str = fmt.replace('{0}', _url).replace('{1}', url);
                            rows[fmt].push(str);
                        }
                    }
                    //
                    let tabTxtArr = [
                        heads.map(h => `| ${h} `).join(''),
                        heads.map(h => `| - `).join('')
                    ];
                    for (const fmt in rows)//row
                        tabTxtArr.push(rows[fmt].map(h => `| ${h} `).join(''));
                    let tabTxt = tabTxtArr.join(`|\n`);
                    let ms = new vscode.MarkdownString();
                    ms.appendMarkdown(`**${url}**\n\n${tabTxt}\n
[](command:extension.showLastWebLog_hover) [](command:editor.action.selectAll) [](command:editor.action.showContextMenu)`);//select 
                    ms.isTrusted = true;
                    resolve(new vscode.Hover(ms));//  ![](https://image.zhangxinxu.com/image/blog/201912/flex-demo-s.gif)
                    /* markdown */
                }, 100);//
            });
        }
    };
    //  C#
    context.subscriptions.push(vscode.languages.registerHoverProvider(
        [{ 
            scheme: 'file', language: 'csharp',
            //  
            pattern: '**/ApiControllers/**'
        }]
        , b));
};